/*form styles*/
#msform {
  text-align: center;
  position: relative;
  margin-top: 30px;
}

#msform fieldset {
  background: white;
  border: 0 none;
  border-radius: 0px;
  box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.4);
  padding: 20px 30px;
  box-sizing: border-box;
  width: 80%;
  max-width: 1000px;
  margin: 0 auto;

  /*stacking fieldsets above each other*/
  position: relative;
}

.section-header {
  display: flex;
}

.form-slider {
  width: 80%;
  margin: auto;
}

.form-group {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  width: 80%;
  margin: auto;
}

.subform-box {
  width: 60%;
  margin: auto
}

.subform-tag {
  background: lightgray;
    padding: 2px 7px;
    min-width: 30px;
    text-align: center;
    border-radius: 15px;
    margin: 0 3px;
}

.subform-box-bar {
  display: flex;
  align-items: baseline;
  flex-wrap: wrap;
}

.checkbox-group {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

/*Hide all except first fieldset*/
#msform fieldset:not(:first-of-type) {
  display: none;
}

/*inputs*/
#msform input,
#msform textarea {
  padding: 15px;
  border: 1px solid #ccc;
  border-radius: 0px;
  margin-bottom: 10px;
  width: 100%;
  box-sizing: border-box;
  font-family: montserrat;
  color: #2c3e50;
  font-size: 13px;
}

#msform input:focus,
#msform textarea:focus {
  box-shadow: none !important;
  border: 1px solid #1e1c1c;
  outline-width: 0;
  transition: All 0.5s ease-in;
  -webkit-transition: All 0.5s ease-in;
  -moz-transition: All 0.5s ease-in;
  -o-transition: All 0.5s ease-in;
}

